import React, { Component } from 'react';
import { Alert } from 'react-bootstrap';
import Form from 'react-bootstrap/Form';
import { useHistory } from 'react-router-dom';
import { useState, useEffect } from 'react';
import "../assets/css/demo.css";
import { Badge, Button, Card, Navbar, Nav, Container, Row, Col } from "react-bootstrap";
import { useDispatch } from 'react-redux';
import { storeUserData } from 'redux/actions/actions';

export default function Login() {
    const dispatch = useDispatch();
    const history = useHistory();
    const [loginEmail, setLoginEmail] = useState('');
    const [loginPassword, setLoginPassword] = useState('');
    const [emailData, setEmailData] = useState({});

    const handleChangeEmail = (e) => {
        let tempemail = [...loginEmail]
        tempemail = e.target.value;
        setLoginEmail(tempemail);
    }
    const handleChangePassword = (e) => {
        let tempPass = [...loginPassword]
        tempPass = e.target.value;
        setLoginPassword(tempPass);
    }

    // useEffect(() => {
    //     // Update the document title using the browser API
    //     console.log(emailData, 'emailData');
    // },[emailData]);

    const onSubmit = (e) => {
        e.preventDefault();

        const data = {
            "email": loginEmail,
            "password": loginPassword,
            "remember": true
        };

        fetch('http://localhost:8080/admin/login', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                
            },
            body: JSON.stringify(data),
        })
            .then(response => response.json())
            .then(data => {
                setEmailData(data);
                localStorage.setItem('data', JSON.stringify(data))
                localStorage.setItem("token",data.data.token )
                console.log(data, "data");
                setTimeout(() => {
                    dispatch(storeUserData(data))
                }, 2000);
                if (data.status === true) {
                    history.push("admin/dashboard")
                }
                else {
                    alert("Please Enter Correct Email or Password")
                }
            })
            .catch((error) => {
                console.error('Error:', error);
            });
    }

    return (
        <>
            <Container>
                <Row>
                    <Col md="5">
                        <Card class="loginbox">
                            <Card.Header>
                                <Card.Title as="h4">User Login</Card.Title>
                            </Card.Header>
                            <Card.Body>
                                <Form>
                                    <Form.Group className="mb-3" controlId="formBasicEmail">
                                        <Form.Label>Email address</Form.Label>
                                        <Form.Control type="email" placeholder="Enter email" onChange={(e) => { handleChangeEmail(e) }} />
                                        <Form.Text className="text-muted">
                                            We'll never share your email with anyone else.
                                        </Form.Text>
                                    </Form.Group>

                                    <Form.Group className="mb-3" controlId="formBasicPassword">
                                        <Form.Label>Password</Form.Label>
                                        <Form.Control type="password" placeholder="Password" onChange={(e) => { handleChangePassword(e) }} />
                                    </Form.Group>
                                    <Form.Group className="mb-2" controlId="formBasicCheckbox">
                                        <Form.Check.Input type="checkbox" label="Remember Me" /><Form.Label>Remember Me</Form.Label>
                                    </Form.Group>
                                    <Button variant="primary" type="submit" onClick={(e) => onSubmit(e)}>
                                        Submit
                                    </Button>
                                </Form>
                            </Card.Body>
                        </Card>
                    </Col>

                </Row>
            </Container>
        </>


    )
}
