import React, { useEffect, useState } from 'react';
import { Badge, Button, Card, Navbar, Nav, Table, Container, Row, Col, Modal, Form } from "react-bootstrap";
import "../assets/css/demo.css";


export default function Usertest() {
    const [posts, setPosts] = useState([]);
    const [que, setQue] = useState([]);
    const [show, setShow] = useState(false);
    const handleClose = () => setShow(false);
    const current = new Date();
  
    useEffect(() => {
        fetchPost()
    }, []);


    const fetchPost = async (index) => {

        const settings = {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json',
                "x-api-token": localStorage.getItem("token")
            }
        };
        try {
            const response = await fetch("http://localhost:8080/admin/testlist", settings);
            const resdata = await response.json();
            console.log(resdata.data, 'jhsdfiuwef')
            setPosts(resdata.data);
            // console.log(res.data);
        } catch (e) {
            res.send("test list cant be shown")
        }
        
    }
    const QueList = async (id) => {
        
        setShow(true);
        const savedata = {
            id: id,
        }
        const settings = {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                "x-api-token": localStorage.getItem("token")
            },
            body: JSON.stringify(savedata)
        };
        try {

            const response = await fetch(`http://localhost:8080/admin/questionlist`, settings);
            const resdata = await response.json();
         
            setQue(resdata.data);

            // console.log(res.data);
        } catch (e) {
            res.send("test list cant be shown")
        }

    }
    return (
        <>
            <Container fluid>
                <Row>
                    <Col md="12">
                        <Card className="strpied-tabled-with-hover">
                            <Card.Header>
                                <Card.Title as="h4">test </Card.Title>
                            </Card.Header>
                            <Card.Body className="table-full-width table-responsive px-0">
                                <Table className="table-hover table-striped">
                                    <thead>
                                        <tr>
                                            <th>Test Name</th>
                                            <th>Start Date</th>
                                            <th>End Date</th>
                                            <th>Action</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {posts && posts.map((item, index) => {
                                            return (
                                                <>
                                                
                                                    <tr key={index}>
                                                   
                                                        <td>
                                                        
                                                            {
                                                                <td>{item.testName}</td>
                                                            }
                                                        </td>
                                                        <td>
                                                            {
                                                                <td>{item.startDate}</td>
                                                            }
                                                        </td>
                                                        <td>
                                                            {
                                                                <td>{item.endDate}</td>
                                                            }
                                                        </td>
                                                        <td>
                                                            {
                                                                new Date(item.startDate) <= new Date(current) && new Date(item.endDate) >= new Date(current) ?
                                                                    <>
                                                                        <Button className='btn btn-outline-primary crudButton' onClick={() => QueList(item.id)}>Attempt</Button>
                                                                    </> : ""
                                                            }
                                                        </td>
                                                    </tr>

                                                </>
                                            )
                                        }
                                        )}

                                    </tbody>

                                </Table>

                                <Modal show={show} onHide={() => setShow(false)}>
                                    <Modal.Header>
                                        <Modal.Title>Questions</Modal.Title>
                                    </Modal.Header>
                                    <Modal.Body>
                                        {que && que.map((item,id) => {
                                            return (
                                                <>
                                              
                                                    <Row key={item.id}>
                                                    {console.log(item.option4)}
                                                        <Col md="12">
                                                            Q.  {
                                                                item.question
                                                            }
                                                        </Col>
                                                        <Col md="12">
                                                            <div key="default-radio" className="mb-3">
                                                                <Form.Check type="radio" name={item.id} id={item.option1} label={item.option1}/>
                                                            </div>
                                                        </Col>
                                                        <Col md="12">
                                                            <div key="default-radio" className="mb-3">
                                                                <Form.Check type="radio" name={item.id} id={item.option2} label={item.option2}/>
                                                            </div>
                                                        </Col>
                                                        <Col md="12">
                                                            <div key="default-radio" className="mb-3">
                                                                <Form.Check type="radio" name={item.id} id={item.option3} label={item.option3}/>
                                                            </div>
                                                        </Col>
                                                        <Col md="12">
                                                            <div key="default-radio" className="mb-3">
                                                                <Form.Check type="radio" name={item.id} id={item.option4} label={item.option4}/>
                                                                
                                                            </div>
                                                        </Col>
                                                     
                                                    </Row>

                                                </>
                                            )
                                        })}
                                    </Modal.Body>
                                    <Modal.Footer>
                                        <Button variant="secondary" onClick={handleClose}>
                                            Close
                                        </Button>
                                        <Button variant="primary">
                                            next
                                        </Button>
                                    </Modal.Footer>
                                </Modal>
                            </Card.Body>
                        </Card>
                    </Col>
                </Row>
            </Container>
        </>
    )
};
