import React, { useState, useEffect } from 'react';
import { Badge, Button, Card, Navbar, Nav, Table, Container, Row, Col, Modal, Form } from "react-bootstrap";



export default function AdminTest() {
    const [show, setShow] = useState(false);
    const [qshow, setQshow] = useState(false);
    const [posts, setPosts] = useState([]);
    const [que, setQue] = useState([]);
    const [queId, setQueId] = useState('')
    const [mshow, setMshow] = useState(false);
    const handleClose = () => setMshow(false);
    const [sindex, setSindex] = useState('');
    const [tindex, setTindex] = useState('');
    const [values, setValues] = useState({
        testName: '',
        startDate: '',
        endDate: '',
        question: '',
        option1: '',
        option2: '',
        option3: '',
        option4: '',
        answer: '',
    })
    useEffect(() => {
        fetchPost()
    }, []);
   



    const fetchPost = async (index) => {

        const settings = {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json',
                "x-api-token": localStorage.getItem("token")
            }
        };
        try {
            const response = await fetch("http://localhost:8080/admin/testlist", settings);
            const resdata = await response.json();
            console.log(resdata.data, 'jhsdfiuwef')
            setPosts(resdata.data);
            // console.log(res.data);
        } catch (e) {
            res.send("test list cant be shown")
        }
    }
    const handleChange = (e) => {
        const { name, value } = e.target;

        setValues({
            ...values,
            [name]: value,
        });
    }


    const createTest = async () => {

        const savedata = {
            testName: values.testName,
            startDate: values.startDate,
            endDate: values.endDate
        }
        console.log(savedata, "savedata-adduser");
        const settings = {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                "x-api-token": localStorage.getItem("token")
            },
            body: JSON.stringify(savedata)
        };
        console.log(settings, 'settings');
        const response = await fetch("http://localhost:8080/admin/createtest", settings);
        console.log(response, 'responce');
        setShow(false);
        fetchPost()
        setValues({
            testName: '',
            startDate: '',
            endDate: '',
        })
    }

    const queOpenModel = (id) => {
        setMshow(true);
        setQueId(id);
    }
    const onDelete = async (item) => {
        let deletedata = {
            id: item.id
        }
        // console.log(deletedata, '--')
        const settings = {
            method: 'DELETE',
            headers: {
                'Content-Type': 'application/json',
                "x-api-token": localStorage.getItem("token")
            },
            body: JSON.stringify(deletedata)
        };
        // console.log(settings);
        const response = await fetch("http://localhost:8080/admin/deleteTest", settings);
        // console.log(response);
        const resdata = await response.json();

        if (response.status === 401) {
            dispatch(storeUserData({}))
            history.push("/admin")
        } else {
            fetchPost();
        }

    }



    const AddQuestion = async (item) => {

        const savedata = {
            id: queId,
            question: values.question,
            option1: values.option1,
            option2: values.option2,
            option3: values.option3,
            option4: values.option4,
            answer: values.answer,
            testId: queId,
        }
        console.log(savedata, "savedata-adduser");
        const settings = {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                "x-api-token": localStorage.getItem("token")
            },
            body: JSON.stringify(savedata)
        };
        console.log(settings, 'settings');
        const response = await fetch("http://localhost:8080/admin/addQuestion", settings);
        console.log(response, 'responce');
        setShow(false);
        handleClose();
        setValues({
            question: '',
            option1: '',
            option2: '',
            option3: '',
            option4: '',
            answer: '',
        })
    }
    const QueList = async (id) => {
        setQshow(true);
        const savedata = {
            id: id,
        }
        const settings = {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                "x-api-token": localStorage.getItem("token")
            },
            body: JSON.stringify(savedata)
        };
        try {

            const response = await fetch(`http://localhost:8080/admin/questionlist`, settings);
            const resdata = await response.json();
            console.log(resdata.data, 'jhsdfiuwef')
            setQue(resdata.data);

            // console.log(res.data);
        } catch (e) {
            res.send("test list cant be shown")
        }

    }
    const onEditTest = (item, index) => {
        setTindex(index);

        values.testName = item.testName,
        values.startDate = item.startDate,
        values.endDate = item.endDate
    }
    const onEditQuestion = (item, index) => {
        setSindex(index);

        values.question = item.question,
        values.option1 = item.option1,
        values.option2 = item.option2,
        values.option3 = item.option3,
        values.option4 = item.option4,
        values.answer = item.answer
    }
    const onCancel = () => {
        setSindex('')
        setTindex('')
    }
    const onSave = async (item,id) => {
        console.log(item,"item");
        let savedata = {
            
            id: item.id,
            question: values.question,
            option1: values.option1,
            option2: values.option2,
            option3: values.option3,
            option4: values.option4,
            answer: values.answer
        }
        const settings = {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                "x-api-token":localStorage.getItem("token")
            },
            body: JSON.stringify(savedata)
        };
        console.log(settings, 'settings');
        const response = await fetch("http://localhost:8080/admin/editQuestion", settings);
        console.log(response, 'responce');
        if (response.status === 401) {
            dispatch(storeUserData({}))
            history.push("/admin")
        }else{
          
        }
        QueList(item.testId);
        setSindex('')
    }

    const onSaveTest = async (item) => {
        console.log(item,"item");
        let savedata = {
            id: item.id,
            testName: values.testName,
            startDate: values.startDate,
            endDate: values.endDate,
        }
        const settings = {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                "x-api-token":localStorage.getItem("token")
            },
            body: JSON.stringify(savedata)
        };
        console.log(settings, 'settings');
        const response = await fetch("http://localhost:8080/admin/editTest", settings);
        console.log(response, 'responce');
        if (response.status === 401) {
            dispatch(storeUserData({}))
            history.push("/admin")
        }else{
            fetchPost()
        }
        setTindex('')
    }


    return (
        <>
            <Container fluid>
                <Row>
                    <Col md="12">
                        <Card className="strpied-tabled-with-hover">
                            <Card.Header>
                                <Card.Title as="h4">Table </Card.Title>
                                <Button variant="primary" onClick={() => setShow(true)}>Create Test</Button>
                            </Card.Header>
                            <Card.Body className="table-full-width table-responsive px-0">


                                <Table className="table-hover table-striped">
                                    <thead>
                                        <tr>
                                            <th>Test Id</th>
                                            <th>Test Name</th>
                                            <th>Start Date</th>
                                            <th>End Date</th>
                                            <th>Action</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {posts && posts.map((item, index) => {
                                            return (
                                                <>
                                                    <tr key={index}>
                                                        <td>
                                                            {
                                                                item.id
                                                            }
                                                        </td>
                                                        <td>
                                                            {
                                                                tindex === index ? <td><input type="text" value={values.testName} name="testName"
                                                                onChange={(e) => handleChange(e)} /></td> : <td>{item.testName}</td>
                                                                
                                                            }
                                                        </td>
                                                        <td>
                                                            {
                                                                 tindex === index ? <td><input type="date" value={values.startDate} name="startDate"
                                                                 onChange={(e) => handleChange(e)} /></td> : <td>{item.startDate}</td>
                                                                
                                                            }
                                                        </td>
                                                        <td>
                                                            {
                                                                tindex === index ? <td><input type="date" value={values.endDate} name="endDate"
                                                                onChange={(e) => handleChange(e)} /></td> : <td>{item.endDate}</td>
                                                              
                                                            }
                                                        </td>
                                                        <td>
                                                            {
                                                                 tindex === index ? <><Button className='btn btn-outline-primary crudButton' onClick={() => onSaveTest(item)}>Save</Button>
                                                                 <Button className='btn btn-outline-primary crudButton' onClick={() => onCancel()}>cancel</Button></>
                                                                 : 
                                                                <>
                                                                    <Button className='btn btn-outline-primary crudButton' onClick={() => queOpenModel(item.id)}>Add Question</Button>
                                                                    <Button className='btn btn-outline-primary crudButton' onClick={() => QueList(item.id)}>Show Question</Button>
                                                                    <Button className='btn btn-outline-danger crudButton' onClick={() => onDelete(item)} >Delete</Button> 
                                                                     <Button className='btn btn-outline-primary crudButton' onClick={() => onEditTest(item, index)}>edit</Button>
                                                                     </>
                                                            }

                                                        </td>
                                                    </tr>
                                                </>
                                            )
                                        }
                                        )}

                                    </tbody>

                                </Table>

                                <Table show={qshow}>

                                    {qshow ?
                                        <thead>

                                            <tr>
                                                <th>Question</th>
                                                <th>option 1</th>
                                                <th>option 2</th>
                                                <th>option 3</th>
                                                <th>option 4</th>
                                                <th>answer</th>
                                                <th>action</th>
                                            </tr>
                                        </thead> : ""}
                                    <tbody>

                                        {que && que.map((item, index) => {
                                            return (
                                                <>

                                                    <tr key={index}>
                                                        <td>
                                                            {
                                                                 sindex === index ? <td><input type="text" value={values.question} name="question"
                                                                 onChange={(e) => handleChange(e)} /></td> : <td>{item.question}</td>
                                                                
                                                            }
                                                        </td>
                                                        <td>
                                                            {
                                                                sindex === index ? <td><input type="text" value={values.option1} name="option1"
                                                                onChange={(e) => handleChange(e)} /></td> : <td>{item.option1}</td>
                                                               
                                                            }
                                                        </td>
                                                        <td>
                                                            {
                                                                sindex === index ? <td><input type="text" value={values.option2} name="option2"
                                                                onChange={(e) => handleChange(e)} /></td> : <td>{item.option2}</td>
                                                                
                                                            }
                                                        </td>
                                                        <td>
                                                            {
                                                                sindex === index ? <td><input type="text" value={values.option3} name="option3"
                                                                onChange={(e) => handleChange(e)} /></td> : <td>{item.option3}</td>
                                                               
                                                            }

                                                        </td>
                                                        <td>
                                                            {
                                                                sindex === index ? <td><input type="text" value={values.option4} name="option4"
                                                                onChange={(e) => handleChange(e)} /></td> : <td>{item.option4}</td>
                                                                
                                                            }
                                                        </td>
                                                        <td>
                                                            {
                                                                sindex === index ? <td><input type="text" value={values.answer} name="answer"
                                                                onChange={(e) => handleChange(e)} /></td> : <td>{item.answer}</td>
                                                               
                                                            }
                                                        </td>
                                                        <td>
                                                            {
                                                                sindex === index ? <><Button className='btn btn-outline-primary crudButton' onClick={() => onSave(item)}>Save</Button>
                                                                    <Button className='btn btn-outline-primary crudButton' onClick={() => onCancel()}>cancel</Button></>
                                                                    : <>
                                                                        <Button className='btn btn-outline-primary crudButton' onClick={() => onEditQuestion(item, index)}>edit</Button>
                                                                        <Button className='btn btn-outline-danger crudButton' onClick={() => onDelete(item)}>Delete</Button> </>
                                                            }
                                                        </td>
                                                    </tr>
                                                </>
                                            )
                                        })}
                                    </tbody>
                                </Table>

                                <Modal
                                    show={show}
                                    onHide={() => setShow(false)}
                                    dialogClassName="modal-90w"
                                >
                                    <Modal.Header>
                                        <Modal.Title id="example-custom-modal-styling-title">
                                            Create Test
                                        </Modal.Title>
                                    </Modal.Header>
                                    <Modal.Body>
                                        <Row>
                                            <Col md="12">
                                                <Form.Label htmlFor="testName">Test Name</Form.Label>
                                                <Form.Control type="Text" id="testName" value={values.testName} name="testName" onChange={(e) => handleChange(e)} required />
                                            </Col>
                                        </Row>
                                        <Row>
                                            <Col md="6">
                                                <Form.Label htmlFor="startDate">startDate</Form.Label>
                                                <Form.Control type="date" id="startDate" value={values.startDate} name="startDate" onChange={(e) => handleChange(e)} required />
                                            </Col>

                                            <Col md="6">
                                                <Form.Label htmlFor="endDate">endDate</Form.Label>
                                                <Form.Control type="date" id="endDate" value={values.endDate} name="endDate" onChange={(e) => handleChange(e)} required />
                                            </Col>
                                        </Row>
                                        <Row>
                                            <Modal.Footer>

                                                <Button onClick={() => createTest()}>Create</Button>

                                            </Modal.Footer>
                                        </Row>

                                    </Modal.Body>
                                </Modal>
                                {/* question modal */}
                                <Modal show={mshow} onHide={handleClose}>
                                    <Modal.Header closeButton>
                                        <Modal.Title>Add Question</Modal.Title>
                                    </Modal.Header>
                                    <Modal.Body>
                                        <Row>
                                            <Col md="12">
                                                <Form.Label htmlFor="question">Question</Form.Label>
                                                <Form.Control type="Text" id="question" value={values.question} name="question" onChange={(e) => handleChange(e)} required />
                                            </Col>
                                        </Row>
                                        <Row>
                                            <Col md="6">
                                                <Form.Label htmlFor="option1">option 1</Form.Label>
                                                <Form.Control type="Text" id="option1" value={values.option1} name="option1" onChange={(e) => handleChange(e)} required />
                                            </Col>
                                            <Col md="6">
                                                <Form.Label htmlFor="option2">option 2</Form.Label>
                                                <Form.Control type="Text" id="option2" value={values.option2} name="option2" onChange={(e) => handleChange(e)} required />
                                            </Col>
                                        </Row>
                                        <Row>
                                            <Col md="6">
                                                <Form.Label htmlFor="option3">option 3</Form.Label>
                                                <Form.Control type="Text" id="option3" value={values.option3} name="option3" onChange={(e) => handleChange(e)} required />
                                            </Col>
                                            <Col md="6">
                                                <Form.Label htmlFor="option4">option 4</Form.Label>
                                                <Form.Control type="Text" id="option4" value={values.option4} name="option4" onChange={(e) => handleChange(e)} required />
                                            </Col>
                                        </Row>
                                        <Row>
                                            <Col md="12">
                                                <Form.Label htmlFor="answer">answer</Form.Label>
                                                <Form.Control type="Text" id="answer" value={values.answer} name="answer" onChange={(e) => handleChange(e)} required />
                                            </Col>

                                        </Row>
                                    </Modal.Body>
                                    <Modal.Footer>
                                        <Button variant="secondary" onClick={handleClose}>
                                            Close
                                        </Button>
                                        <Button variant="primary" onClick={() => AddQuestion()}>
                                            Add Question
                                        </Button>
                                    </Modal.Footer>
                                </Modal>

                            
                            </Card.Body>
                        </Card>
                    </Col>
                </Row>
            </Container>
        </>
    )
};
