
import { useState } from "react";
import { Badge, Button, Card, Navbar, Nav, Table, Container, Row, Col, Modal } from "react-bootstrap";
import Form from 'react-bootstrap/Form'


var options = {};
export default function addUser() {
    const [values, setValues] = useState({
        name: '',
        contact: '',
        email: '',
        password: '',
    })

    const handleChange = (e) => {
        const { name, value } = e.target;

        setValues({
            ...values,
            [name]: value,
        });
    }
    const onSave = async (e) => {
        e.preventDefault();
        const savedata = {
            name: values.name,
            contact: values.contact,
            email: values.email,
            password: values.password,

        }
        console.log(savedata, "savedata-adduser");
        const settings = {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                "x-api-token":localStorage.getItem("token")
            },
            body: JSON.stringify(savedata)
        };
        console.log(settings, 'settings');
        const response = await fetch("http://localhost:8080/admin/save", settings);
        console.log(response, 'responce');
    }
    return (
        <>
            <Container fluid>
                <Row>
                    <Col md="12">
                        <Card className="strpied-tabled-with-hover">
                            <Card.Header>
                                <Card.Title as="h4">Add User </Card.Title>

                            </Card.Header>
                            <Card.Body className="table-full-width table-responsive px-0">
                                <Form className="ml-3 mr-3">
                                    <Form.Group className="mb-3" controlId="formBasicname">
                                        Full Name:
                                        <Form.Control type="name" placeholder="Enter name" value={values.name} name="name" onChange={(e) => handleChange(e)} required />
                                    </Form.Group>

                                    <Form.Group className="mb-3" controlId="formBasicEmail">
                                        Email address:
                                        <Form.Control type="email" placeholder="Enter email" value={values.email} name="email" onChange={(e) => handleChange(e)} required />
                                        <Form.Text className="text-muted">
                                            We'll never share your email with anyone else.
                                        </Form.Text>
                                    </Form.Group>

                                    <Form.Group className="mb-3" controlId="formBasicPassword">
                                        Password
                                        <Form.Control type="password" placeholder="Password" value={values.password} name="password" onChange={(e) => handleChange(e)} required />
                                    </Form.Group>

                                    <Form.Group className="mb-3" controlId="formBasiccontact">
                                        Contact No.
                                        <Form.Control type="number" placeholder="contactno" value={values.contact} name="contact" onChange={(e) => handleChange(e)} required />
                                    </Form.Group>




                                    <Button variant="primary" type="submit" onClick={(e) => onSave(e)}>
                                        Submit
                                    </Button>
                                </Form>
                            </Card.Body>
                        </Card>
                    </Col>
                </Row>
            </Container>

        </>
    )
}
